body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-color: #f7f9fc;
  color: #333;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
}

h1, h2, h3 {
  margin: 0;
  font-weight: 600;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

textarea {
  width: 100%;
  max-width: 100%;
  height: 100px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}

.container {
  padding: 20px;
}

.response, .logs {
  margin-top: 20px;
  padding: 15px;
  background-color: #e9f7ef;
  border-left: 5px solid #28a745;
  border-radius: 5px;
}
.username-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0; /* 위아래 여백 */
  padding: 10px;
  background-color: #f5f5f5; /* 배경 색상 */
  border-radius: 10px; /* 부드러운 모서리 */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
}

.username-container label {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.username-container .username-input {
  width: 80%;
  max-width: 300px; /* 모바일 화면을 고려한 최대 너비 */
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease-in-out;
}

.username-container .username-input:focus {
  border-color: #007bff; /* 포커스 시 색상 */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); /* 포커스 시 그림자 */
}
.footer {
  margin-top: 40px; /* 위쪽에 추가 여백 */
  padding: 20px;
  text-align: center;
  font-size: 14px;
  color: #888;
  background-color: #f9f9f9;
  border-top: 1px solid #ccc; /* 상단 경계선 */
}